.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 14px 28px;
  gap: 2px;

  width: 196px;
  height: 50px;

  background: #ffcb40;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: none;

  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;

  text-transform: uppercase;

  color: #373737;
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #ffba00;
}
